.map {
  position: relative;
}

.map__contact {
  padding: 26px 20px;
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    padding: 60px 70px;
    max-width: @tablet-width;
    margin: 0;

    &--span {
      display: block;
      margin-top: 20px;
    }
  }

  @media (min-width: @desktop-width) {
    background-color: #ffffff;
    position: absolute;
    top: 95px;
    left: 110px;
    z-index: 2;
    width: 565px;
  }
}

.map__contact h3 {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0;
  text-align: left;
  text-transform: uppercase;
  box-sizing: border-box;
  margin: 0;
  min-width: 140px;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 26px;
    max-width: 165px;
  }
}

.map__contact p {
  font-family: "Lato-Regular", sans-serif;
  font-style: normal;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0;
  text-align: left;
  margin: 0;

  @media (min-width: @tablet-width) {
    text-align: end;
    font-size: 16px;
    line-height: 20px;
    max-width: 160px;
  }
}

.map__picture {
  position: relative;
  width: 320px;
  height: 362px;
  margin: 0;
  padding: 0;

  @media (min-width: @tablet-width) {
    min-width: 768px;
    min-height: 400px;
  }

  @media (min-width: @desktop-width) {
    min-width: 1440px;
    min-height: 400px;
  }

  &::after {
    content: "";
    position: absolute;
    left: 133px;
    top: 138px;
    display: block;
    width: 57px;
    height: 53px;
    background-image: url("data:image/svg+xml,%3Csvg width='57' height='53' viewBox='0 0 57 53' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg clip-path='url(%23clip0_95_146)'%3E%3Crect width='57' height='53' fill='url(%23pattern0)'/%3E%3C/g%3E%3Cdefs%3E%3Cpattern id='pattern0' patternContentUnits='objectBoundingBox' width='1' height='1'%3E%3Cuse xlink:href='%23image0_95_146' transform='scale(0.00884956 0.00943396)'/%3E%3C/pattern%3E%3CclipPath id='clip0_95_146'%3E%3Crect width='57' height='53' fill='white'/%3E%3C/clipPath%3E%3Cimage id='image0_95_146' width='113' height='106' xlink:href='data:image/png;base64,'/%3E%3C/defs%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: center;

    @media (min-width: @tablet-width) {
      left: 331px;
      top: 150px;
      width: 113px;
      height: 106px;
      background-image: url("data:image/svg+xml,%3Csvg width='113' height='106' viewBox='0 0 113 106' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg clip-path='url(%23clip0_94552_4088)'%3E%3Crect width='113' height='106' fill='url(%23pattern0)'/%3E%3C/g%3E%3Cdefs%3E%3Cpattern id='pattern0' patternContentUnits='objectBoundingBox' width='1' height='1'%3E%3Cuse xlink:href='%23image0_94552_4088' transform='scale(0.00884956 0.00943396)'/%3E%3C/pattern%3E%3CclipPath id='clip0_94552_4088'%3E%3Crect width='113' height='106' fill='white'/%3E%3C/clipPath%3E%3Cimage id='image0_94552_4088' width='113' height='106' xlink:href='data:image/png;base64,'/%3E%3C/defs%3E%3C/svg%3E");
      background-size: cover;
    }

    @media (min-width: @desktop-width) {
      left: 905px;
      top: 130px;
      width: 113px;
      height: 106px;
    }
  }
}

.map__contact img {
  object-fit: cover;
}
